  methods: {
    resetStatus() {
      showToast("重置所有参数");
    },
    updateMotorsStatus() {
      this.$axios.get("http://orin-desktop.local:5000/motor").then(res => {
        let motors = res.data.motors;
        for (let id in motors) {
          const motor = motors[id];
          this.motorStatus[motor["id"]] = {
            "timestamp": motor["timestamp"],
            "position": motor["position"],
            "velocity": motor["velocity"],
            "torque": motor["torque"]
          };
        }
      });
    },
    onMotorStatusChange(id, key, value) {
      this.motorStatus[id][key] = value;
      let motor = this.motorStatus[id];
      const status = {
        "kd": 1,
        "kp": 3,
        "id": id,
        "control_type": 'MOTOR_MIT_CONTROL',
        "position": motor["position"],
        "velocity": motor["velocity"],
        "torque": motor["torque"]
      };
      status[key] = value;
      console.log(status);
      this.$axios.put("http://orin-desktop.local:5000/motor/control", status, {
        params: {
          "id": id
        }
      }).then(res => {
        showToast(res.data);
      });
    }
  }
